.header {
  width: 20rem;
  height: 100%;
  border-right: 1px solid #28313b;
  padding-top: 2rem;
  position: fixed;
  top: 0;
  left: 0;
}

.header p {
  font-size: var(--regular-size);
}

.logo-premium {
  align-items: center;
  padding: 0 2rem 1.6rem 1.6rem;
  display: flex;
}

.logo-premium img {
  width: 4.8rem;
  margin-right: 2.4rem;
}

.premium-nav {
  border-radius: 1.2rem;
  padding: .5rem 1rem;
}

.user {
  background-color: var(--control-background-color);
  justify-content: space-between;
  align-items: center;
  padding: 1.6rem;
  display: flex;
}

.user-avatar {
  width: 2.4rem;
  border-radius: 50%;
  margin-right: .7rem;
}

.user i {
  color: var(--color-second);
  padding-left: 1rem;
  font-size: 1.6rem;
}

.main-nav-list {
  flex-direction: column;
  padding: 1.6rem 0;
  display: flex;
}

.main-nav-btn {
  margin-bottom: .2rem;
  display: block;
}

.main-nav-item {
  cursor: pointer;
  align-items: center;
  padding: .8rem 0 .8rem 1.6rem;
  display: flex;
  position: relative;
}

.main-nav-item.active-nav {
  background-color: #28313b;
}

.main-nav-item i {
  padding-right: 1.2rem;
  font-size: 1.8rem;
}

.search-icon {
  background: linear-gradient(to top right, #00b509, #77e254);
  font-size: 2rem;
}

.house-icon {
  background: linear-gradient(to top right, #287be5, #29d7ff);
}

.compass-icon {
  background: linear-gradient(to top right, #ff9b2d, #ffe443);
}

.headphone-icon--md {
  background: linear-gradient(to top right, #1a82e5, #df22f2);
}

.chart-icon {
  background: linear-gradient(to top right, #f44336, #ffb353);
}

.music-icon {
  background: linear-gradient(to top right, #27b3e2, #13f8e5);
}

.icon-header-kit {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.library-heading {
  margin: .5rem 0;
  padding-left: 1.6rem;
}

.main-nav-item:before {
  content: "";
  height: 100%;
  width: .2rem;
  background: linear-gradient(to right, #2daaed 0%, #2daaed 100%) 50% / .2rem 0% no-repeat;
  border-radius: 2rem;
  transition: all .16s linear;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.main-nav-item.active-nav:before, .main-nav-btn:hover .main-nav-item:before {
  background-size: .2rem 100%;
}

.left-sidebar {
  width: 32rem;
  height: 100%;
  background-color: var(--control-background-color);
  border-left: 1px solid #2c353f;
  padding: 1.6rem 2.4rem;
  position: fixed;
  top: 0;
  right: 0;
}

.left-side-song-des {
  background-color: #252e39;
  border-radius: .6rem;
  padding: 1.4rem 1.6rem 1.6rem;
}

.left-side-img {
  width: 100%;
}

.left-side-img-container {
  border-radius: .4rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 16px rgba(0, 0, 0, .2);
}

.song-play-name, .song-play-singer, .left-side-album {
  text-decoration: none;
  display: block;
}

.song-play-name {
  color: rgba(244, 246, 248, .88);
  opacity: .9;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 1.2rem 0;
  font-size: 1.4rem;
  font-weight: 600;
  overflow: hidden;
}

.song-play-singer {
  color: rgba(244, 246, 248, .5);
  font-size: 1.3rem;
  line-height: 1.2rem;
}

.left-side-album {
  text-align: center;
  color: rgba(244, 246, 248, .5);
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-top: 1.6rem;
  font-size: 1.3rem;
  overflow: hidden;
}

.left-side-song-control {
  z-index: 2;
  width: 100%;
  height: 19.6rem;
  color: rgba(244, 246, 248, .5);
  background-color: #212a35;
  flex-direction: column;
  padding: 20px 23px 0;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.list-control {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.control-slider {
  margin: auto;
  position: relative;
}

.input-slider {
  width: 100%;
  height: 2px;
  color: #239def;
  z-index: 3;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
}

.input-slider:hover {
  color: #239def;
}

.time-passed-container {
  width: 100%;
  justify-content: space-between;
  display: flex;
  position: absolute;
}

.list-control ion-icon {
  cursor: pointer;
}

.volumn-icon, .volumn-icon--mute {
  font-size: 1.4rem;
  display: block;
}

.volumn-icon-contain {
  z-index: 4;
  position: absolute;
}

.control-volumn {
  z-index: 3;
  cursor: pointer;
  position: relative;
}

.volumn-input {
  opacity: 0;
  cursor: pointer;
  background-color: #18222d;
  border-radius: 3.2rem;
  justify-content: center;
  padding: 1.6rem 1.6rem 1.6rem 4rem;
  transition: opacity .3s;
  display: none;
  position: absolute;
  top: -45px;
  left: -50px;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  box-shadow: 0 .2rem .4rem rgba(0, 0, 0, .2);
}

.volumn-icon-wrapper, .option-icon-wrapper {
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.control-volumn:hover .volumn-input {
  opacity: 1;
  display: flex;
}

.volumn-input input {
  width: 72px;
  height: 2px;
  background-color: rgba(244, 246, 248, .1);
}

.option-icon {
  font-size: 1.8rem;
}

.left-side-song-control .list-control:nth-child(1), .left-side-song-control .list-control:nth-child(3) {
  margin-bottom: 2rem;
  padding: 0 1.2rem;
}

.left-side-song-control .list-control:nth-child(2) {
  margin-bottom: 2.2rem;
}

.btn-control-list {
  color: rgba(244, 246, 248, .5);
  cursor: pointer;
  background-color: rgba(244, 246, 248, .02);
  border: none;
  border-radius: 1.6rem;
  padding: 1rem 3.2rem;
}

.slider-rail {
  width: 18.4rem;
  height: 2px;
  background-color: #3d454f;
}

.random i, .prev-song i, .next-song i, .loop-song i {
  cursor: pointer;
  padding: 1rem;
  font-size: 1.6rem;
}

.loop-song i {
  font-size: 1.8rem;
}

.loop-1 {
  opacity: 0;
  color: #2daaed;
  transition: all .3s;
  position: absolute;
  top: 12px;
  right: 18px;
}

.loop-1 i {
  font-size: .7rem;
}

.loop-1-song .loop-1 {
  opacity: 1;
}

.loop-1-song i, .loop-alist-song i {
  color: #2daaed;
}

.play-pause-song {
  width: 50px;
  height: 50px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.play-pause-song i {
  cursor: pointer;
  font-size: 3.6rem;
}

.play-song, .pause-song {
  display: none;
}

.show {
  display: block;
}

.random:hover, .prev-song:hover, .next-song:hover, .loop-song:hover, .play-pause-song:hover {
  background-color: rgba(244, 246, 248, .05);
  border-radius: 50%;
}

.left-song-quality {
  text-align: center;
}

.left-song-quality button {
  color: rgba(244, 246, 248, .5);
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1rem;
}

.random-active i {
  color: #2daaed;
}

.main-content, .main-content-searching {
  max-width: 100%;
  margin: 0 32rem 0 20rem;
  padding: 2.2rem 0 6.4rem;
  display: none;
  overflow: hidden;
}

.main-content.main-content--active {
  display: block;
}

.main-container {
  max-width: 100%;
  margin-left: 1.6rem;
  position: relative;
  overflow: hidden;
}

.main-container:hover .slide-scrolls {
  opacity: 1;
}

.slider {
  width: 100%;
  margin: auto;
  padding: 0 1.6rem;
  display: flex;
}

.slider-top {
  min-width: 100%;
  opacity: .5;
  -webkit-transform: all .7s;
  transform: all .7s;
  padding: 0 .5rem;
  overflow: hidden;
}

.slider-top img {
  width: 100%;
  object-fit: contain;
  border-radius: .5rem;
}

.slider-top.slide-active {
  opacity: 1;
}

.slide-scrolls {
  width: 100%;
  opacity: 0;
  justify-content: space-between;
  padding: 0 3rem;
  transition: all .5s;
  display: flex;
  position: absolute;
  top: 50%;
}

.slide-prev, .slide-next {
  cursor: pointer;
  background-color: rgba(0, 0, 0, .5);
  border: none;
  border-radius: 5px;
  padding: 2.2rem 1.2rem;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.icon-scroll {
  color: #fff;
  font-size: 3rem;
}

.container-dot {
  text-align: center;
  margin-top: 1rem;
}

.dots__dot {
  width: .6rem;
  height: .6rem;
  cursor: pointer;
  background-color: rgba(244, 246, 248, .1);
  border: none;
  border-radius: .4rem;
  margin: 0 .3rem;
  transition: all .3s;
}

.active-dot.dots__dot {
  background-color: var(--color-third);
  width: 1.8rem;
}

.home-gallery {
  overflow: hidden;
}

.home-item {
  margin: 2.4rem 2.4rem 4.8rem;
  position: relative;
  overflow: hidden;
}

.control-slick {
  position: absolute;
  top: 0;
  right: .8rem;
}

.slick-list {
  width: 100%;
  justify-self: space-between;
  -webkit-text-size-adjust: 100%;
  align-items: center;
  margin: auto;
  display: flex;
}

.spot-item {
  opacity: 0;
  min-width: 25%;
  vertical-align: baseline;
  padding: 0 .8rem;
  transition: all .5s;
  position: relative;
}

.topic-description {
  padding: 0 8px;
}

.slick-active {
  opacity: 1;
}

.spot-img-play {
  width: 100%;
  cursor: pointer;
  overflow: hidden;
}

.spot-img-play img {
  width: 100%;
  object-fit: contain;
}

.control-slick {
  justify-content: space-between;
  align-items: center;
  column-gap: .8rem;
  display: flex;
}

.control-left i, .control-right i {
  color: var(--disable-color);
  font-size: 1.8rem;
}

.control-active i {
  color: rgba(244, 246, 248, .5);
  cursor: pointer;
}

.slick-list .slick-item:nth-child(6) {
  left: 0;
}

.home-item {
  position: relative;
}

.slick-slide-title {
  white-space: nowrap;
  text-overflow: ellipsis;
}

.spot-img-play {
  opacity: 1;
  transition: opacity .3s linear, -webkit-transform .3s linear, transform .3s linear;
}

.item-container-play {
  border: 1px solid rgba(244, 246, 248, .05);
  border-radius: .4rem;
  margin-bottom: 1rem;
  position: relative;
  overflow: hidden;
}

.item-container-play:hover .spot-img-play {
  opacity: .6;
  -webkit-transform: scale(1.14);
  transform: scale(1.14);
}

.spot-item-onplay-btn {
  width: 2rem;
  height: 2rem;
  opacity: 0;
  background-color: rgba(244, 246, 248, .88);
  border: none;
  border-radius: 50%;
  transition: opacity .3s;
  position: absolute;
  bottom: 2%;
  right: 2%;
}

.spot-item-onplay-btn i {
  color: #1a82e5;
}

.home-play-btn {
  cursor: pointer;
  z-index: 1;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.item-container-play:hover .home-play-btn, .spot-item-active.item-container-play .spot-item-onplay-btn {
  opacity: 1;
}

.spot-item-active.item-container-play .home-play-btn {
  opacity: 0;
  display: none;
}

.spot-item-active.item-container-play:hover .spot-img-play {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.home-play-btn i {
  color: rgba(244, 246, 248, .88);
  font-size: 3.4rem;
  transition: all .1s linear;
}

.home-play-btn:hover i {
  font-size: 4rem;
}

.new-song-content-item {
  background-color: #212a35;
  margin-bottom: 2.4rem;
  padding: 2rem 4rem;
  display: flex;
}

.new-song-content-item img {
  width: 2.4rem;
}

.header-second--pad {
  padding-left: 3.2rem;
}

.new-song-description {
  width: 100%;
  margin-left: 4rem;
}

.new-song-name {
  margin-bottom: 1.2rem;
  display: block;
}

.new-song-name:hover {
  color: var(--color-third);
}

.new-song-img {
  width: 16rem;
  height: 16rem;
  object-fit: contain;
  border-radius: 4px;
  overflow: hidden;
}

.new-song-img img {
  width: 100%;
  display: block;
}

.new-song-singer {
  align-items: center;
  margin-bottom: .8rem;
  display: flex;
}

.singer-avatar-container {
  display: flex;
}

.singer-avatar-container img {
  border: 1px solid #fdf2e9;
  border-radius: 50%;
  margin-right: -.6rem;
}

.song-date-release {
  border-bottom: 1px solid rgba(244, 246, 248, .05);
  padding-top: 3px;
  padding-bottom: 2rem;
}

.song-date-release span {
  color: var(--color-second);
  padding-left: .3rem;
  font-size: 1.3rem;
  text-decoration: none;
}

.song-date-release i {
  color: var(--color-second);
  font-size: 1.4rem;
}

.new-songs-list {
  width: 58rem;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
  position: relative;
}

.new-song-content-item {
  display: none;
}

.new-song-content-item.active {
  display: block;
}

.new-song-content-item--wrapper {
  display: flex;
}

.singer-name {
  width: 90%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.singer-avatar-container {
  margin-right: 2rem;
}

.new-song-item {
  width: 20%;
  cursor: pointer;
  padding: 0 .8rem;
  transition: all .3s linear;
  position: relative;
}

.new-song-item img {
  width: 100%;
  border-radius: 3px;
}

.new-song-item button {
  width: 2rem;
  height: 2rem;
}

.new-song-item i {
  font-size: 2.6rem;
}

.new-song-item.active > button {
  opacity: 1;
}

.new-song-item.active img {
  opacity: .6;
}

.new-songs-list--point {
  z-index: 10;
  border: .8rem solid rgba(0, 0, 0, 0);
  border-top: 1rem solid rgba(244, 246, 248, .05);
  transition: all .3s;
  position: absolute;
  top: -24px;
  left: 41px;
}

.ranking-containter {
  width: 100%;
  justify-content: center;
  column-gap: 1.6rem;
  padding: 0 3.2rem;
  display: flex;
}

.ranking-item {
  width: 33.3333%;
  position: relative;
}

.ranking-img-item {
  position: relative;
}

.ranking-background {
  height: 16rem;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 4px;
}

.ranking-bg-item-1 {
  background-image: url("https://stc-id.nixcdn.com/v12/static/media/bg_top_1.03d0fd3f.jpg");
}

.ranking-bg-item-2 {
  background-image: url("https://stc-id.nixcdn.com/v12/static/media/bg_top_2.9eb21c0e.jpg");
}

.ranking-bg-item-3 {
  background-image: url("https://stc-id.nixcdn.com/v12/static/media/bg_top_3.a057d449.jpg");
}

.ranking-name {
  text-transform: uppercase;
  color: #fff;
  padding-top: 3.6rem;
  padding-left: 2.4rem;
  font-size: 2.8rem;
}

.ranking-song-content {
  text-overflow: ellipsis;
  white-space: nowrap;
  background-color: #212a35;
  border-radius: 0 0 4px 4px;
  padding: 48px 24px 0;
  overflow: hidden;
}

.ranking-song-content-item {
  display: none;
}

.ranking-song-content-item.active {
  display: block;
}

.ranking-song-name {
  color: rgba(244, 246, 248, .88);
  font-size: 16px;
  font-weight: 500;
}

.ranking-song-name:hover {
  color: var(--color-third);
  cursor: pointer;
}

.ranking-song-singer {
  margin: 1.6rem 0 2.4rem;
}

.ranking-img-item {
  cursor: pointer;
  background-color: #1d2631;
  border-radius: 3px;
  position: absolute;
  top: 9.6rem;
  overflow: hidden;
}

.ranking-img-item img {
  z-index: 1;
  width: 8rem;
}

.ranking-img-list .ranking-img-item:nth-child(1) {
  z-index: 3;
  left: 2.4rem;
}

.ranking-img-list .ranking-img-item:nth-child(2) {
  z-index: 2;
  left: 22%;
}

.ranking-img-list .ranking-img-item:nth-child(3) {
  z-index: 1;
  left: 35.3%;
}

.play-rank-btn.home-play-btn {
  width: 40px;
  z-index: 4;
  opacity: 1;
  border: none;
  position: absolute;
  top: 28px;
  left: calc(100% - 28px);
}

.ranking-img-list .ranking-img-item:hover {
  z-index: 3;
}

.ranking-img-item:hover img {
  opacity: .6;
}

.ranking-position {
  color: rgba(244, 246, 248, .05);
  font-size: 5.6rem;
  font-weight: 700;
  position: absolute;
  bottom: 4.8rem;
  right: 3.2rem;
}

.home-video-hot {
  padding: 0 3.2rem;
}

.video-list {
  grid-template-columns: repeat(6, 1fr);
  gap: 1.6rem;
  display: grid;
}

.video-item {
  position: relative;
}

.video-duration {
  height: 16px;
  color: rgba(255, 255, 255, .88);
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, .6));
  padding-left: 14px;
  padding-right: 2px;
  font-size: 10px;
  line-height: 16px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.video-play {
  border-radius: 4px;
  overflow: hidden;
}

.video-play img {
  width: 100%;
}

.video-list .video-item:nth-child(1) {
  grid-column: 1 / 3;
}

.video-list .video-item:nth-child(2) {
  grid-column: 3 / 5;
}

.video-list .video-item:nth-child(3) {
  grid-column: 5 / 7;
}

.video-item {
  padding-bottom: 4.8rem;
  position: relative;
}

.video-content {
  width: 100%;
  position: absolute;
  bottom: 1.2rem;
  left: 0;
}

.video-name {
  color: rgba(244, 246, 248, .88);
  padding: .8rem 0;
  font-size: 1.4rem;
  font-weight: 500;
  text-decoration: none;
}

.video-singer {
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.video-name:hover, .video-singer-name:hover {
  color: var(--color-third);
}

.video-singer-name {
  color: rgba(244, 246, 248, .5);
  margin: 6px 4px;
  font-size: 1.3rem;
  text-decoration: none;
  display: inline-block;
}

.video-item .link-subject {
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 1.4rem;
  overflow: hidden;
}

.home-songs {
  padding: 0 3.2rem;
}

.home-song-list, .search-song-container {
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  gap: .4rem 1.6rem;
  display: grid;
}

.song-list-item {
  width: 100%;
  background-color: #212a35;
  border-radius: 4px;
  padding: 8px;
  display: flex;
  position: relative;
}

.song-list-item.song-list-item-home, .song-description-container {
  width: 100%;
}

.song-number-listen, .icon-more-option {
  position: absolute;
}

.song-list-item:hover .vip-icon {
  display: none;
}

.vip-icon {
  position: absolute;
  top: 35%;
  right: 2.3rem;
}

.vip-icon i {
  color: #ffc40e;
  font-size: 1.8rem;
}

.song-item-img {
  padding-right: 8px;
}

.song-item-img img {
  width: 5.4rem;
  border-radius: 3px;
}

.song-item-name {
  width: 90%;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 1.4rem;
  display: block;
  overflow: hidden;
}

.song-item-name:hover {
  color: rgba(244, 246, 248, .88);
  cursor: default;
}

.song-number-listen {
  color: rgba(244, 246, 248, .5);
  font-size: 1.3rem;
  display: flex;
  top: 4rem;
  right: 1.6rem;
